import { Axios } from 'axios'
import {
  Token,
  TokenData,
  TokenRoutes,
  TokensClientModel,
  TokensSearchQuery,
} from '../token.model'
import { ApiEntity } from '../../core'

export class TokensClient extends ApiEntity implements TokensClientModel {
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a token
   * @param body - the token data
   * @returns the created token
   */
  public create(body: TokenData) {
    return this._create<Token>(TokenRoutes.v1Create, body)
  }

  /**
   * Search tokens by params
   * @param searchParams - the filters to apply
   * @returns a tokens page
   */
  public search(searchParams?: TokensSearchQuery) {
    return this._search<Token>(TokenRoutes.v1Search, searchParams)
  }

  /**
   * List tokens by params
   * @param searchParams - the filters to apply
   * @returns the token list
   */
  public list(searchParams?: TokensSearchQuery) {
    return this._list<Token>(TokenRoutes.v1Search, searchParams)
  }

  /**
   * Find a token by params
   * @param searchParams - the filters to apply
   * @returns an optional token
   */
  public findOne(searchParams?: TokensSearchQuery) {
    return this._findOne<Token>(TokenRoutes.v1Search, searchParams)
  }

  /**
   * Read a token by params or throw
   * @param searchParams - the filters to apply
   * @returns the token
   */
  public readOne(searchParams: TokensSearchQuery) {
    return this._readOne<Token>(TokenRoutes.v1Search, searchParams)
  }
}
